import React from 'react';
import './style/index.global.less';
export default class extends React.Component {
    constructor(props) {
        super(props);
        this.menuData = {};
        this.domDidMount = (div) => {
            if (!div) {
                console.log('容器丢失');
                return;
            }
            const toolKey = div.getAttribute('data-tool-key');
            if (!toolKey) {
                console.log('data-tool-key 属性丢失');
                return;
            }
            const toolParams = { ...this.menuData[toolKey] };
            toolParams.params.lang = this.props.toolParams.lang;
            toolParams.url = toolParams.params.url;
            this.pluginLoaderManager.loadPlugin(toolParams, div, {}, () => {
                const child = div.getElementsByTagName('img')[0];
                div.removeChild(child);
            });
        };
        const toolParams = props.toolParams;
        const wnd = window;
        this.pluginLoaderManager = wnd.globalCoreDictionary.pluginLoaderManager;
        const gridCfg = wnd.g_ctx.getCfgByKey('gridCfg') || [];
        for (const item of gridCfg) {
            if (item.title === toolParams.title) {
                this.gridData = item.grid;
                break;
            }
        }
        if (!this.gridData) {
            throw new Error(`${toolParams.title}的页面功能内容未进行配置`);
        }
        this.gridData.forEach(item => {
            const toolid = wnd.m_vt[item.toolKey];
            const options = wnd.m_tools[toolid];
            item.params = { ...options };
            this.menuData[item.toolKey] = item;
        });
        this.loading = toolParams.loading || '';
        this.loadingWidth = toolParams.loadingWidth ? parseInt(toolParams.loadingWidth) : 224;
        this.loadingHeight = toolParams.loadingHeight ? parseInt(toolParams.loadingHeight) : 224;
    }
    renderLoading() {
        if (this.loading) {
            const imgStyle = {
                height: this.loadingHeight,
                width: this.loadingWidth,
            };
            if (/(jpg|jpeg|png|gif)$/i.test(this.loading)) {
                return <img src={this.loading} style={imgStyle} alt="loading"/>;
            }
            else if (/(svg)$/i.test(this.loading)) {
                return <img src={this.loading} style={imgStyle} alt="loading"/>;
            }
        }
        return <img src="./images/main/loading.gif" alt="loading"/>;
    }
    render() {
        return (<div className="grid">
        {this.gridData.map((item, index) => {
            const itemStyle = {
                ...(item.style || {}),
                top: item.top,
                left: item.left,
                width: item.width,
                height: item.height,
            };
            return (<div className="item" style={itemStyle} ref={this.domDidMount} data-tool-key={item.toolKey} key={index}>
              {this.renderLoading()}
            </div>);
        })}
      </div>);
    }
}
